.root {
    position: fixed;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    z-index: 100;
    background-color: rgba(0, 0, 0, 0.8);

    &.is-loading {
        .webcam {
            opacity: 0;
        }
    }
}

.webcam {
    width: 100%;
    height: 100%;
    object-fit: cover;

    opacity: 1;

    transition: 0.3s ease;
    transition-property: opacity;
}

.loader {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
}

.button {
    position: absolute;

    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);

    width: 48px;
    height: 48px;
    border-radius: 50%;

    background-color: #fff;
    border: none;
    outline: none;

    &::after {
        content: "";

        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        border-radius: 50%;
        border: 2px solid #fff;
        box-sizing: border-box;

        width: 60px;
        height: 60px;
    }
}